import React        from 'react'
import ReactDOM     from 'react-dom'

import {
    _Date,
}                               from 'JS/services.mjs'

import {
    store,
    SELECTORS,
    bookingsDataUPD,
    periodUPD,
    setUser,

    getBookingsData,
    bookingsDataToObj
}                               from 'index'

import App                      from 'components/App.jsx'




function ERROR_LOGGER(err, id = 'global') {
    var stack   =  err.error && err.error.stack
    var message = `ERR_logger ${id}: \n`
                + `${ (err && err.error) || err }`
    if (stack) {
        message += '\n' + stack
    }

    if ( typeof message === 'object') {
        message = JSON.stringify(message)
    }

    console.trace(message)
    return message
}
window.addEventListener( 'error', ERROR_LOGGER )
window.onload = start



async function start() {
    renderApp()

    setUser()
    getPeriod()
    // updBookings() //updated in periodUPD
}// end start

function renderApp() {
    ReactDOM.render(
        <App />,
        document.getElementById('root')
    )
}


function updBookings() {
    let upd = SELECTORS.bookingsUpd( store.getState() )
    if (Date.now() -upd > 600000) reloadBookings()
}

async function loadBookings( period ) {
    let bookingsData = await getBookingsData( period )
    let bookingsObj  = bookingsDataToObj( bookingsData )

    store.dispatch( bookingsDataUPD( {
        'obj': bookingsObj,
        'raw': bookingsData,
    } ) )
}
function reloadBookings() {
    let period = getPeriod()
    loadBookings( period )
}


function getPeriod(reqDate) {
    let period = SELECTORS.period( store.getState() )

    if (reqDate || !period.reqDate) {
        period = setNewPeriod(reqDate)
    }

    return period
}

function setNewPeriod(reqDate = new Date()) {
    let stDate         = new Date(reqDate)
        stDate.setDate(stDate.getDate() - 7) //also take data for prev day

    let endDate        = new Date(reqDate)
        endDate.setDate(endDate.getDate() + 21)

    reqDate =_Date.dateToYYYYMMDD(reqDate, '-')
    stDate  =_Date.dateToYYYYMMDD(stDate, '-')
    endDate =_Date.dateToYYYYMMDD(endDate, '-')

    store.dispatch( periodUPD( {reqDate, stDate, endDate} ) )

    loadBookings( {stDate, endDate} )

    return {
        reqDate,
        stDate,
        endDate
    }
}




export {
    start,
    getPeriod,
    reloadBookings,
}