
import { Provider }                  from 'react-redux'
import { useRoutes, useRedirect }    from 'hookrouter'

import 'CSS/App.css'

import { store }         from 'JS/store.js'


import BottomBar         from 'components/BottomBar.jsx'
import ButtonBar         from 'components/ButtonBar.jsx'
import BookingsTable     from 'components/BookingsTable.jsx'
import FreetimeTable     from 'components/FreetimeTable.jsx'


const routes = {
    '*/CRA_FTime_GITPAGE/bookings': () => ({mode: 'bookings', Table: BookingsTable}),
    '*/CRA_FTime_GITPAGE/freetime': () => ({mode: 'freetime', Table: FreetimeTable}),
}


function App() {
    useRedirect('*/CRA_FTime_GITPAGE', '/CRA_FTime_GITPAGE/bookings')
    const {mode, Table} = useRoutes(routes)
    return (
        <Provider store={ store }>
            <div className="App">
                <ButtonBar mode = { mode }/>
                <div id = "inner" className="inner">
                    <Table />
                </div>
                <BottomBar />
            </div>
        </Provider>
    )
}

export default App











