
import {useRoutes, navigate}    from 'hookrouter'

import {
    useSelector,
    useDispatch,
}                               from 'react-redux'

import {
    testActionMD,
    minTimeUPD,
    SELECTORS,
}                               from 'JS/store.js'

import { _Date }                from 'JS/services.mjs'
import {
    getPeriod,
    logIn,
    logOut,
    reloadBookings,
}                               from '../index'



import {useBus, evDispatch}             from 'JS/useBus'


const modeMap = {
    'bookings': {href: '/freetime', HMBtn: <HomeButton />},
    'freetime': {href: '/bookings', HMBtn: <MinTimeButton />}
}

const Spacer = () => <span style={{width: '1em'}}></span>




function ReqDateButton() {
    let { reqDate: reqDateStr } = useSelector( SELECTORS.period )
    function setReqDate() {
        let reqDateInput = document.getElementById('reqDateInput')

        let reqDate = reqDateInput.value
        reqDate     = new Date(reqDate)
        getPeriod(reqDate)
    }

    return (
        <button id="reqDateButton" className="reqDateButton">
            <span id="reqDateInputText">{ reqDateStr || '...' }</span>
            <input type="date" id="reqDateInput" name="reqDate"
                onChange={setReqDate}></input>
        </button>
)}

function ReloadButton() {

    // useBus(
    //     '@@ui/NEXT_NAV_BTN_CLICK',
    //     () => console.dir('ReloadButton NEXT_NAV_BTN_CLICK detected')
    // )

    return(
        <button id="reloadButton"
            onClick={ reloadBookings }
            fontWeight="800"
        ><b>&#08635;</b>
        </button>
)}

function LoginButton() {
    const dispatch   = useDispatch()

    function loginButtonClick(e) {
        if ( e.target.innerText === 'Log in') {
            dispatch( testActionMD( 'logIn()'  ) )
            logIn()
        }
        else if ( window.confirm('?') ) {
            dispatch( testActionMD( 'logOut()' ) )
            logOut()
        }
    }

    return(
        <button id="loginButton"
        onClick={(e) => loginButtonClick(e)}
        minwidth="3em">Log in</button>
)}




function HomeButton() {
    // useBus(
    //     '@@ui/NEXT_NAV_BTN_CLICK',
    //     () => console.dir('HomeButton NEXT_NAV_BTN_CLICK detected')
    // )


    function homeButtonClick() {
        evDispatch('@@ui/HOME_BTN_click')
    }

    return (
        <button id="homeButton"
            onClick    ={ homeButtonClick }
            fontWeight ="800"
            minwidth   ="2em"
        ><b></b>
        </button>
    )
}

function MinTimeButton () {
    const dispatch  = useDispatch()
    let minTime     = useSelector( SELECTORS.minTime )
    function setNewMintime() {
        let minTime = prompt('Looking for ... (min)', 15)
        dispatch( minTimeUPD( minTime ) )
    }

    return(
        <button id="setMintimeButton"
            onClick={ setNewMintime }
            >{minTime + '\'' || '...'}</button>
    )
}



function ModeButtonRTD({href}) {
    return(
        <button
          id="routedModeButton"
          onClick={ () => {
                        navigate(href)
                        console.dir('call ' + href)
                } }
          minwidth="2em"
        >&#10059;</button>
    )
}


function ButtonBar({mode}) {
    const {HMBtn, href} = modeMap[mode]

    return (
        <div className="buttonBar">
            <ReqDateButton />
                <Spacer />
            {HMBtn}
                <Spacer />
            <ReloadButton />
                <Spacer />
            <ModeButtonRTD href={href}/>
                <Spacer />
            <LoginButton />

        </div>
    )
}

export default ButtonBar
