
import {
    _Date,
}                   from 'JS/services.mjs'

import {
    APP_CONSTANTS,
}                   from 'index'



function bookingsDataToObj(bookingsData) {
    const {timeCI, timeValCI, restStCI, colsCnt} = APP_CONSTANTS.bookingsDataMap

    const restRecTmp  = new Array(colsCnt - restStCI).fill('')
    let resObj        = {}

    for (const dateStr in bookingsData) {
        const dateObj         = _Date.msToCustomDateObj( Date.parse(dateStr) )
        const timeblocks      = bookingsData[dateStr]
        let targetBloksObj    = proceedTimeblocks(timeblocks, dateObj)
        resObj[dateObj.dateN] = targetBloksObj
    }

        //debugger
        console.log(resObj)

    return resObj



    function proceedTimeblocks(timeblocks, dateObj) {
        let targetBloksObj = {}
        for (const blockName in timeblocks) {
            const block        = timeblocks[blockName]
            const blockMs      = _Date.timeStrToMS( blockName )
            const blockN       = _Date.msToSlotN( blockMs )
            const blockId      = block.id
            let targetBlockRec = proceedBlock(block, blockMs, dateObj)
            targetBloksObj[blockN] = targetBlockRec
        }
        return targetBloksObj
    }

    function proceedBlock(block, blockMs, dateObj) {
        const blockTimeVal  = 30 -block.minutesAvailable
        const bookingsArr   = block.bookedFlights

        let targetBlockRec  = []
            targetBlockRec[timeCI]       = blockMs
            targetBlockRec[timeValCI]    = blockTimeVal
            targetBlockRec[restStCI]     = []

        let blockBookingsArr = targetBlockRec[restStCI]

        if (bookingsArr.length === 0) {
            blockBookingsArr.push( restRecTmp.slice() )
        } else {
            proceedBookingsArr(bookingsArr, blockBookingsArr, dateObj)
        }

        return targetBlockRec
    }

    function proceedBookingsArr(bookingsArr, targetBookingsArr) {
        bookingsArr.forEach(bookingObj => {
            let timeVal     = bookingObj.minutes
            let tariff      = bookingObj.tariff.name
            let flyer       = bookingObj.flyerName
            let note        = bookingObj.notes + bookingObj.flyerNotes
            let bookingNo   = bookingObj.id
            let payment     = bookingObj.status.name
                              + (bookingObj.invoice || bookingObj.certificate || '')
            let email       = bookingObj.flyerEmail
            let phone       = bookingObj.flyerPhone

            let targetRec = [
                timeVal,
                tariff,
                flyer,
                note,
                bookingNo,
                payment,
                email,
                phone,
            ]

            targetBookingsArr.push(targetRec)
        })

        return targetBookingsArr
    }
}



export {
    bookingsDataToObj,
}