
import {
    setButtonText,
}                               from 'JS/services.mjs'

import {
    store,
    userDataUPD,
    SELECTORS,
}                               from 'JS/store.js'



function setUser() {
  if (localStorage.getItem('user') &&
      localStorage.getItem('userPs')) {

    let user = {
        name     : localStorage.getItem('user'),
        userPs   : localStorage.getItem('userPs'),
        data     : localStorage.getItem('userData'),
    }
    setButtonText('loginButton', user.name.slice(0, 6))
  } else {
    logIn()
  }
}

function getUser() {
    return SELECTORS.userObj( store.getState() )
}

function logIn() {
  let user = prompt('   (e-mail)  ', '')
  let ps = prompt('    ', '')
  if (!user || !ps) {
    alert('user and password required!')
    return
  }
  storeUser(user, ps)
  setButtonText('loginButton', user.slice(0, 6))
}
// eslint-disable-next-line no-unused-vars
function storeUser(user, ps) {
  localStorage.setItem('user', user)
  localStorage.setItem('userPs', ps)
  store.dispatch( userDataUPD( {name: user, userPs: ps} ) )
}
function logOut() {
  localStorage.removeItem('user')
  localStorage.removeItem('userPs')
  store.dispatch( userDataUPD( {name: '', userPs: ''} ) )
  setButtonText('loginButton', 'Log in')
}
// eslint-disable-next-line no-unused-vars




export {
    getUser,
    setUser,
    logIn,
    logOut,
}