async function getBookingsData ({stDate, endDate, days=5, validation, modification, source}) {
    console.log('gtBkngD', stDate, endDate)
    const SOURCE = source ||'FS_API'//'XENIA_ONLINE'//'FS_API'//'TEST'
    const user   = getUser()

    stDate   = new Date(stDate)
    endDate  = new Date(endDate)

    if ( isNaN(stDate) )   stDate = new Date()
    if ( isNaN(endDate) ) {
        endDate = new Date(stDate)
        endDate.setHours( (days-1)*24, 0, 0, 0)
    }

    let url, params, options

    switch (SOURCE) {
        case 'XENIA_ONLINE':
            url     = APP_CONSTANTS.googleReserveURL
            params  = {
                reqSheet: 'XENIA_ONLINE', //'TEST'//'LAST_DB'//'XENIA_ONLINE'
                stDate,
                days:     7
            }
            options = {'method': 'GET'}

            validation = (fetchResult) => !!(
                fetchResult.startsWith?.('[[')
                && fetchResult.endsWith?.(']]')
                && fetchResult.length > 100
            )

            modification = (bookingsData) => {
                return JSON.parse(bookingsData)
            }
          break

        case 'FS_API':
            url     = APP_CONSTANTS.GAS_PROXY_URL
            options = {
                method: 'GET',
            }
            params  = {
                comand: 'getBookingsData',
                dateFr: _Date.dateToYYYYMMDD(stDate,  '-'),
                dateTo: _Date.dateToYYYYMMDD(endDate, '-'),
            }

            validation = (fetchResult) => {
                try {
                    fetchResult = JSON.parse(fetchResult)
                    return !!(
                        fetchResult.status == 'ok'
                     && fetchResult.data?.dates?.length > 0
                    )
                } catch (error) {
                    console.log('FETCH_ERROR', error)
                    console.log('FETCH_DATA', fetchResult)
                    return false
                }
            }

            modification = (bookingsData) => {
                bookingsData = JSON.parse(bookingsData).data
                return bookingsData.timeblocks
            }
          break

        case 'TEST':
            url    = null
            params  = {
                stDate,
                days:     22
            }
            validation = () => false
          break
    }

    let bookingsData = await fetchAPI(url, {params, options})

    if ( !validation(bookingsData) ) {
        //debugger
        console.dir('fetched bookings is INVALID!')//
        switch (SOURCE) {
            case 'TEST':
                return await asyncGetTestData('generator', {cnt: 1000, reqDate: stDate})//CSV0106
            default:
                // return await asyncGetTestData('generator', {cnt: 1000, reqDate: stDate})//CSV0106
                return [[null]]
        }
    }

    if (modification) {
        bookingsData = modification(bookingsData)
    }

    return bookingsData
}//end getBkngD


