
// import {createStore}                                    from 'redux'
import { configureStore, createSlice, } from '@reduxjs/toolkit'


export const APP_CONSTANTS = {
    bookingsDataMap: {
      timeCI:     0,
      timeValCI:  1,
      restStCI:   2,
      colsCnt:    10,
    },

    BT_DATES_OFFSET: {
        stOffset:   -1,
        endOffset:   3
    },



    googleReserveURL:    'https://script.google.com/macros/s/'
                        +'AKfycbzTar1UNak29DSl87L1DXeclMxdD8ZDfK1zL2FeiaJklwx416Jr6qe72Dd4GT8ZMXI/exec',

    GAS_PROXY_URL:       'https://script.google.com/macros/s/'
                        +'AKfycbySZ6bNzjDGssd3Splxn8Pi-VoY8PKR3FQLc5ESsdvUk4PtoQ0Jxh1bczq82o4VpBWY/exec',

    shiftChangeAppUrl:    'https://nwpoul.github.io/ShiftChangeWEB/Index.html'
}

const APP_VARS = {
    user: {
        name:   '',
        userPs: '',
        data:   {},
    },

    bookingsData: {
        raw: '',
        arr: [],
        obj: {},
        upd: 0,
    },

    period: {
        reqDate: '',
        stDate:  '',
        endDate: '',
    },

    event: '',

    bookingsTable: {
        tableDateN:    null,
        curTbodyInfo: {
            headerStr: 'not set yet',
            dateN:     ''
        },
        testText:     'bookingsTable initial test text'
    },

    freetimeTable: {
        minTime:       15,
        testParameter: 'freetimeTable initial test parameter'
    },

    testState: {
        testText: 'initial test text',
        tsv1: 'test val 1',
        tsv2: 'test val 2',
    }
}

function logger({ getState }) {
    return next => action => {
        console.log('ACT: ', action.type, action.payload)
        return next(action)
    }
}


export const mainStateSlice = createSlice({
    name: 'mainState',

    initialState: {
        user:         APP_VARS.user,
        period:       APP_VARS.period,
        bookingsData: APP_VARS.bookingsData,
        event:        APP_VARS.event,
    },

    reducers: {
        userDataUPD: (state, action) => {
            Object.assign(state.user, action.payload)
        },
        periodUPD: (state, action) => {
            state.period = action.payload
        },
        bookingsDataUPD: (state, action) => {
            Object.assign(state.bookingsData, action.payload)
        },
        bookingsDayDataUPD: (state, action) => {
            let {dayN, dayData}      = action.payload
            //debugger
            if (!dayData) {
                delete state.bookingsData.obj[dayN]
            } else {
                state.bookingsData.obj[dayN] = dayData
            }
        },
        EventUPD: (state, action) => {
            state.event = action.payload
        },
    },
})

export const bookingsTableState = createSlice({
    name: 'bookingsTable',
    initialState: APP_VARS.bookingsTable,

    reducers: {
        tbodyInViewUPD: (state, action) => {
            state.curTbodyInfo = action.payload
        },
        tableDateUPD: (state, action) => {
            state.tableDateN = action.payload
        },
    },
})

export const freetimeTableState = createSlice({
    name: 'freetimeTable',
    initialState: APP_VARS.freetimeTable,

    reducers: {
        minTimeUPD: (state, action) => {
            state.minTime = action.payload
        },
    },
})


export const testState = createSlice({
    name: 'testState',
    initialState: APP_VARS.testState,

    reducers: {
        testActionMD: (state, action) => {
            state.testText = JSON.stringify(action.payload)
        },
        testValUPD: (state, action) => {
            state.tsv1 = action.payload
        },
    }
})



//ACIONS
export const {
    userDataUPD,
    periodUPD,
    bookingsDataUPD,
    bookingsDayDataUPD,
    EventUPD,
} = mainStateSlice.actions

export const {
    tbodyInViewUPD,
    tableDateUPD,
} = bookingsTableState.actions

export const {
    minTimeUPD,
} = freetimeTableState.actions

export const {
    testActionMD,
    testValUPD,
} = testState.actions


export const SELECTORS = {
    bookingsObj : (state) => state.mainState.bookingsData.obj,
    bookingsDayObj : dayN => state => state.mainState.bookingsData.obj[dayN],
    bookingsObjDateNs : dateNsArr => state => Object.fromEntries(
        dateNsArr.map( dateN => [dateN, state.mainState.bookingsData.obj[dateN] ])
    ),

    bookingsUpd : (state) => state.mainState.bookingsData.upd,

    userObj     : (state) => state.mainState.user,
    period      : (state) => state.mainState.period,
    event       : (state) => state.mainState.event,

    curTbodyInfo: (state) => state.bookingsTable.curTbodyInfo,
    tableDateN  : (state) => state.bookingsTable.tableDateN,

    minTime     : (state) => state.freetimeTable.minTime,


    testText    : (state) => state.testState.testText,
}

export const store = configureStore({
    reducer: {
        mainState:     mainStateSlice.reducer,
        bookingsTable: bookingsTableState.reducer,
        freetimeTable: freetimeTableState.reducer,
        testState:     testState.reducer,
    },
    middleware: (getDefaultMiddleware) => getDefaultMiddleware().concat(logger),
})



export function tbInViewUpd_ThunkCreator(newTbodyInfo) {
    return (dispatch, getState) => {
        const {stOffset, endOffset} = APP_CONSTANTS.BT_DATES_OFFSET

        const curTableDateN     = SELECTORS.tableDateN( getState() )
        const curViewDateN      = SELECTORS.curTbodyInfo( getState() ).dateN
        const newViewDateN      = newTbodyInfo.dateN

        let newTableDateN =
            ( newViewDateN == +curTableDateN + stOffset &&
              newViewDateN == curViewDateN - 1 ) ?
                +curTableDateN + stOffset

          : ( newViewDateN == +curTableDateN + endOffset &&
              newViewDateN == +curViewDateN +1 ) ?
                +curTableDateN + endOffset

          : null

        console.log(`THUNK: ViewDate: ${curViewDateN || 'n/a'} -> ${newViewDateN} tableDate: ${curTableDateN} -> ${newTableDateN}`)

        if (newTableDateN) {
            dispatch( tableDateUPD(newTableDateN))
        } else {
            dispatch( tbodyInViewUPD(newTbodyInfo))
        }
    }
}