import React, {
    memo,
    useState,
    useEffect,
    useRef,
    useLayoutEffect,
    useImperativeHandle,
    forwardRef,
    Fragment
}                               from 'react'

import {
    useSelector,
    useDispatch,
    shallowEqual,
}                               from 'react-redux'

import { APP_CONSTANTS }        from '..'
import {_Date }                 from 'JS/services.mjs'
import { useIntersect,
         usePrevious
}                               from 'JS/customHooks.js'

import {
    store,
    tbodyInViewUPD,
    bookingsDayDataUPD,
    tableDateUPD,
    testActionMD,
    tbInViewUpd_ThunkCreator,
    SELECTORS,
}                               from 'JS/store.js'



import {useBus, evDispatch}     from 'JS/useBus'


const StopwatchSVG = <svg width="14" height="14" viewBox="0 0 16 16">
    <path d="M8 3.019v-1.019h2v-1c0-0.552-0.448-1-1-1h-3c-0.552 0-1 0.448-1 1v1h2v1.019c-3.356 0.255-6 3.059-6 6.481 0 3.59 2.91 6.5 6.5 6.5s6.5-2.91 6.5-6.5c0-3.422-2.644-6.226-6-6.481zM11.036 13.036c-0.944 0.944-2.2 1.464-3.536 1.464s-2.591-0.52-3.536-1.464c-0.944-0.944-1.464-2.2-1.464-3.536s0.52-2.591 1.464-3.536c0.907-0.907 2.101-1.422 3.377-1.462l-0.339 4.907c-0.029 0.411 0.195 0.591 0.497 0.591s0.527-0.18 0.497-0.591l-0.339-4.907c1.276 0.040 2.47 0.555 3.377 1.462 0.944 0.944 1.464 2.2 1.464 3.536s-0.52 2.591-1.464 3.536z">
    </path></svg>

const ADMIN_TOGGLE    = true

const BT_HEADER_USER  = [ 'Time', StopwatchSVG, 'tarif', 'Flyers', 'Notes', 'Booking ', 'paid' ]
const BT_HEADER_ADM   = [ 'mail','phone']
const BT_HEADER_ARR   = ADMIN_TOGGLE ? BT_HEADER_USER.concat(BT_HEADER_ADM) : BT_HEADER_USER

const COLS_CNT        = BT_HEADER_ARR.length

const BT_DATES_OFFSET = APP_CONSTANTS.BT_DATES_OFFSET

const BT_HEADER_COLS_ARR = BT_HEADER_ARR.slice(1).map( (item, i) =>
    <th key={i} className = "th-header">
        {item}
    </th>
)

const BT_SERV_TBODY = () => (
    <tbody className = "bt-serv-tbody">
        <tr>
            <td colSpan={COLS_CNT} className = "bt-serv-td">
                <br /><br /><br /> NO DATA <br />
            </td>
        </tr>
    </tbody>
)

const BT_EVENTS = {
    '@@ui/TEST_BTN':
        (dispatch, tableDateN, endOffset) => () => {
            dispatch( tableDateUPD(tableDateN+endOffset))
    },

    '@@ui/TEST_BTN2':
        (dispatch, newDateN) => () => {
            dispatch( tbInViewUpd_ThunkCreator(newDateN))
    },

    '@@ui/TEST_BTN3': () => () => {
        // bookingsDataToObj(bookingsData)
    },
}





export function BookingsTable() {
    const {stOffset, endOffset} = BT_DATES_OFFSET
    const dispatch    = useDispatch()
    const reqDateStr  = useSelector(SELECTORS.period, shallowEqual).reqDate
    const recDateMs   = new Date(reqDateStr).getTime() || Date.now()
    const reqDateN    = _Date.dateMsToDateN(recDateMs)
    // console.trace('BT_RENDERED ' + reqDateN )//
    const tableDateN  = useSelector(SELECTORS.tableDateN)


    useEffect(() => {
       dispatch( tableDateUPD(reqDateN) )
       setTimeout(() => {
           scrollToDay(reqDateN)
       }, 500);
    }, [reqDateN, dispatch])

    const dateNsArr   = Array.from( {length: 1 + endOffset - stOffset},
                            ( _, i) => +tableDateN + (stOffset + i)
                        )

    useBus(
        '@@ui/TEST_BTN3',
        BT_EVENTS['@@ui/TEST_BTN3'](dispatch, tableDateN),
        [tableDateN]
    )

    return  <table id="mainTable" className="bookingsTable">
                <BookingsTableHeader />
                <TbodyesSet dateNsArr = {dateNsArr}/>
            </table>
}

function DayInViewDiv() {
    const dispatch      = useDispatch()
    const curTbodyInfo  = useSelector(SELECTORS.curTbodyInfo)
    const curDateN      = curTbodyInfo.dateN
    const testOnClik    = () => dispatch( testActionMD( 'HEAD DIV Click ' +Date.now() ) )

    useBus(
        '@@ui/NAV_BTN_CLICK',
        (ev) => scrollToDay(curDateN, ev.payload),
        [curDateN]
    )

    return (
        <div
            id        = 'curDayDiv'
            className = 'curDayDiv'
            onClick   = { testOnClik }
        >
            {curTbodyInfo.headerStr}
        </div>
    )
}
function BookingsTableHeader (props) {
    return (
        <tbody id="btHeaderTbody">
            <tr id="btHeaderTr" className="tableHeader">
                <th id="th0" className="th0" >
                    {BT_HEADER_ARR[0]}
                    <DayInViewDiv />
                </th>
                {BT_HEADER_COLS_ARR}
            </tr>
        </tbody>
    )
}


function TbodyesSet({dateNsArr}) {
    const bookingDataObj = useSelector(SELECTORS.bookingsObjDateNs(dateNsArr), shallowEqual)
    let   needDataFlag   = false
    const tbodiesArr = dateNsArr.map( (curDateN, i) => {
        if ( bookingDataObj[curDateN]) {
            return <DateTbody
                key          = {curDateN}
                dateN        = {curDateN}
                dayDataObj   = {bookingDataObj[curDateN]}
            />
        } else {
            needDataFlag = true
            return <BT_SERV_TBODY key = {curDateN}/>
        }
    })

    if (needDataFlag) {//debugger
     console.log('need Data', dateNsArr[0])}
    return tbodiesArr
}

const    TbodyHeaderOnClik = () => evDispatch('@@ui/TBHEADER_CLICK', ('header Click ' + Date.now() ))
function TbodyHeader({dateObj, headerStr, colsCnt}) {

    return (
        <tr id        = {dateObj.dateN + '_0' }
            className = {dateObj.HDay ? 'newDayTr HDay' : 'newDayTr'}
        >
            <th>
                {dateObj.dayName},
                {dateObj.dayN} / {dateObj.monthN}
            </th>
            <td></td>
            <td colSpan = {colsCnt-1}
                onClick = {TbodyHeaderOnClik}
            >
                {headerStr}
            </td>
        </tr>
    )
}

function DateTbody({dateN, dayDataObj}) {
    const { colsCnt, curDateObj,
            headerStr, tbodyId, rowsJSXArr }  = getDateTbodyVals({dayDataObj, dateN})

    const dispatch                            = useDispatch()
    const { intersectFunc, intersectOptions } = setTBodyIntersect({
            tbodyData: {headerStr, dateN},
            dispatcher: dispatch
    })
    const targetRef   = useIntersect(intersectFunc, intersectOptions)

    return (
        <tbody
            ref       = {targetRef}
            id        = {tbodyId}
            className = "bookingsTable"
        >
            <TbodyHeader
                dateObj      = {curDateObj}
                headerStr    = {headerStr}
                colsCnt      = {colsCnt}
            />
            {rowsJSXArr}
        </tbody>
    )
}




function getDateTbodyVals({dayDataObj, dateN}) {
    const {timeCI, timeValCI} = APP_CONSTANTS.bookingsDataMap
    const colsCnt             = COLS_CNT
    const minFreeTime         = 2

    const nowTime             = Date.now() - _Date.GMToffset
    const curDateObj          = _Date.msToCustomDateObj(dateN *_Date.hr24)

    const tbodyId             = dateN
    const headerStr           = getHeaderStrForDay(curDateObj)

    const rowsJSXArr = []
    for (const tSlot in dayDataObj) {
        let curTSlotN    = Number(tSlot)
        let curTime      = curDateObj.timeMS + dayDataObj[tSlot][timeCI]
        let curTimeStr   = _Date.msToCustomDateObj( curTime ).timeStr

        let freeTime     = 30 - dayDataObj[tSlot][timeValCI]
        let rowSpan      = ( freeTime >= minFreeTime && freeTime < 30) ? 2 : null

        let trId         = `${dateN}_${curTimeStr}`
        let trClassStr   = getTrClassList({freeTime, minFreeTime, timeSlotN: curTSlotN, curTime, nowTime})

        let curThJSX     = doThJSX(rowSpan, curTimeStr)
        let curTdJSXArr  = doTdJSXArr(freeTime, tSlot)
        let curTrJSX     =  <tr id={trId} className={trClassStr} key={trId}>
                                {curThJSX}{curTdJSXArr}
                            </tr>

        rowsJSXArr.push(curTrJSX)

        if(rowSpan) {
            rowsJSXArr.push(
                <tr className={trClassStr} key={"adTr" +tSlot} >
                    { doFreetimeTdJSX(colsCnt, freeTime) }
                </tr>
            )
        }
    }//end do Tr for tSlots



    return {colsCnt, curDateObj, tbodyId, headerStr, rowsJSXArr}



        function doThJSX(rowSpan, timeStr) {
            let ThJSX = <th rowSpan={rowSpan} >
                            {timeStr}
                            <br/>
                            <span className="tdSpan"></span>
                        </th>
            return ThJSX
        }//end sub for Th
        function doFreetimeTdJSX (colsCnt, freeTime) {
            return (
                <td colSpan={colsCnt} className="freeTimeTd" >
                    --  {freeTime ? freeTime + ' .' : ''} --
                </td>
            )
        }//end sub fot vacantTd
        function doTdJSXArr(freeTime, tSlot) {
            const restStCI    = APP_CONSTANTS.bookingsDataMap.restStCI

            if (freeTime >= 30) return doFreetimeTdJSX(colsCnt)

            // [[c1][c2][c3]      [[c1/n  [c2/n  [c3/n
            //  [c1][c2][c3]  =>    c1/n   c2/n   c3/n
            //  [c1][c2][c3]]       c1  ]  c2  ]  c3  ]]
            let curArr    = dayDataObj[tSlot][restStCI].slice(1)
            let initValue = dayDataObj[tSlot][restStCI][0].slice()
            let pArr = curArr.reduce(
                (acc, sRow) => {
                    sRow.forEach( (ci,i) => { acc[i] += `\n${ci}` } )
                    return acc
                },
                initValue
            )

            // [ [t,t,t][][] ] => [td(t,t,t),td,td]
            let tdJSXArr = pArr.map(
                (element, i) => (
                    <td key={'pf'+i} className="pf">
                        <pre>{element}</pre>
                    </td>
                )
            )
            return tdJSXArr
        }//end sub fot Td
}// END dayObj to TbodyJSX

function getHeaderStrForDay(dateObj) {
    let dateStr   = _Date.dateToLongStrRu(dateObj.date)
    let headerStr = `${dateStr} ${dateObj.HDay ? ' ()':' ()'}`
    return headerStr
}

function getTrClassList({freeTime, minFreeTime, timeSlotN, curTime, nowTime}) {
    const dayNight  = (timeSlotN <= 17)         ? 'groupN '      : 'groupD '
    const oddity    = (timeSlotN % 2)           ? 'odd '         : 'even '
    const noTime    = (freeTime <= minFreeTime) ? 'noTime-book ' : ''
    const pastTime  = (curTime < nowTime )      ? 'pastSlot '    : ''

    return dayNight + oddity + noTime + pastTime
}









function scrollToDay(curDateN, direction = 'cur') {
    const scrollOptions = {
        block:    "center",
    }
    const targetDateN = {
        cur:  curDateN,
        prev: +curDateN - 1,
        next: +curDateN + 1,
    }

    const targetNode = document.getElementById(targetDateN[direction])
    if (!targetNode) {
        //debugger
         console.dir(`Last loaded ${curDateN} tbody reached!`)//
         return
    }
    console.dir(`scroll to ${targetDateN[direction]} (${targetNode.id})`)//
    targetNode.scrollIntoView(scrollOptions)
}

function setTBodyIntersect({tbodyData, dispatcher})  {
    const intersectOptions = {
        root:          null,
        rootMargin:    '-20% 0% -80% 0%',
    }

    const intersectFunc = (e) => {
        const {
            isIntersecting,
        } = e[0]

        if (!isIntersecting) return

        dispatcher(tbInViewUpd_ThunkCreator(tbodyData))
    }

    return {intersectOptions, intersectFunc}
}







export default BookingsTable
